.header {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 100rem;
  padding: 0 3rem;
}

@keyframes fade-up {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.title {
  font-size: 12rem;
  margin-bottom: 12px;
  text-align: left;
  line-height: 1;
  animation: fade-up 650ms 400ms backwards;
}

.header__para {
  font-size: 2.4rem;
  line-height: 2;
  max-width: 45rem;
  animation: fade-up 650ms 600ms backwards;
}

.social__list {
  margin-top: 1.6rem;
  display: flex;
  animation: fade-up 650ms 800ms backwards;
}

.social__link {
  color: white;
  padding: 0.6rem;
  width: 4.8rem;
  height: 4.8rem;
  font-size: 2.8rem;
  margin-right: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
