.shape {
  position: fixed;
  filter: invert(60%) sepia(79%) saturate(2476%) hue-rotate(86deg)
    brightness(30%) contrast(119%);
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* row */
.shape--0 {
  top: 15vh;
  left: 5vw;
  animation: fade-in 800ms 800ms backwards;
}
.shape--1 {
  top: 15vh;
  left: 50vw;
  animation: fade-in 800ms 800ms backwards;
}
.shape--2 {
  top: 15vh;
  left: 80vw;
  animation: fade-in 800ms 800ms backwards;
}
/* row */
.shape--3 {
  top: 50vh;
  left: 5vw;
  animation: fade-in 800ms 800ms backwards;
}
.shape--4 {
  top: 50vh;
  left: 50vw;
  animation: fade-in 800ms 800ms backwards;
}
.shape--5 {
  top: 50vh;
  left: 80vw;
  animation: fade-in 800ms 800ms backwards;
}
/* row */
.shape--6 {
  top: 80vh;
  left: 5vw;
  animation: fade-in 800ms 800ms backwards;
}
.shape--7 {
  top: 80vh;
  left: 50vw;
  animation: fade-in 800ms 800ms backwards;
}
.shape--8 {
  top: 80vh;
  left: 80vw;
  animation: fade-in 800ms 800ms backwards;
}
